import React, { Component } from 'react';
import { Doughnut } from 'react-chartjs-2';
import person1 from '../../img/person1.jpg';
import person2 from '../../img/person2.jpg';
import person3 from '../../img/person3.jpg';

if (typeof window !== 'undefined') {
  window.$ = $;
  window.jQuery = $;
  require('materialize-css');
}
var x = {
  labels: ['January', 'February', 'March', 'April', 'May', 'June', 'July'],
  datasets: [
    {
      label: 'My First dataset',
      backgroundColor: 'rgb(255, 99, 132)',
      borderColor: 'rgb(255, 99, 132)',
      data: [0, 10, 5, 2, 20, 30, 45]
    }
  ]
};
export default class Visitors extends Component {
  componentDidMount() {}

  render() {
    return (
      <section class="section section-visitors grey lighten-3">
        <div class="row">
          <div class="col s12 m6 l8">
            <div class="card-panel">
              <Doughnut data={x} />
            </div>
          </div>
          <div class="col s12 m6 l4">
            <ul class="collection with-header latest-comments">
              <li class="collection-header">
                <h5>Latest Comments</h5>
              </li>
              <li class="collection-item avatar">
                <img src={person1} alt="" class="circle" />
                <span class="title">John Doe</span>
                <p class="truncate">
                  Lorem ipsum dolor sit amet consectetur adipisicing elit.
                  Nesciunt minima dolor error laboriosam autem ad beatae
                  explicabo pariatur maxime fuga sed quod quo voluptas, adipisci
                  illum aspernatur est, fugit tempore.
                </p>
                <a href="" class="approve green-text">
                  Approve
                </a>{' '}
                |
                <a href="" class="deny red-text">
                  Deny
                </a>
              </li>
              <li class="collection-item avatar">
                <img src={person2} alt="" class="circle" />
                <span class="title">Steve Smith</span>
                <p class="truncate">
                  Lorem ipsum dolor sit amet consectetur adipisicing elit.
                  Nesciunt minima dolor error laboriosam autem ad beatae
                  explicabo pariatur maxime fuga sed quod quo voluptas, adipisci
                  illum aspernatur est, fugit tempore.
                </p>
                <a href="" class="approve green-text">
                  Approve
                </a>{' '}
                |
                <a href="" class="deny red-text">
                  Deny
                </a>
              </li>
              <li class="collection-item avatar">
                <img src={person3} alt="" class="circle" />
                <span class="title">Ellen Williams</span>
                <p class="truncate">
                  Lorem ipsum dolor sit amet consectetur adipisicing elit.
                  Nesciunt minima dolor error laboriosam autem ad beatae
                  explicabo pariatur maxime fuga sed quod quo voluptas, adipisci
                  illum aspernatur est, fugit tempore.
                </p>
                <a href="" class="approve green-text">
                  Approve
                </a>{' '}
                |
                <a href="" class="deny red-text">
                  Deny
                </a>
              </li>
            </ul>
          </div>
        </div>
      </section>
    );
  }
}
